// @flow

import Link from 'next/link';
import Head from 'next/head';
import {hostname} from '../config';
import {Container, Header, Content, Footer} from './layout';
import Post, {StyledFooter} from './layout/post';
import Button from './button';
import CodeBlock from './code-block';
import {MDXProvider} from '@mdx-js/react';

export {Post};

export default function Layout({children}: {children: React.Node}) {
    return (
        <Container>
            <Header />
            <Content>{children}</Content>
            <Footer />
        </Container>
    );
}

const customMDXComponents = {
    wrapper: PostLayoutWrapper,

    pre({children}: {children: React.Element<any>}) {
        return <CodeBlock {...children.props} />;
    },

    li({children}: {children: React.Element<any>}) {
        return <li>{children.props ? children.props.children : children}</li>;
    }
};

export function withMDX(Component: React.ComponentType<any>) {
    return function MDXComponent(props: {}) {
        return (
            <MDXProvider components={customMDXComponents}>
                <Component {...props} />
            </MDXProvider>
        );
    };
}

function PostHead({title, excerpt, author}: PostMeta) {
    return (
        <Head>
            <title>
                {hostname} &nbsp;&bull;&nbsp; {title}
            </title>
            <meta name="description" content={excerpt} />
            <meta name="author" content={author} />
            <meta name="og:type" content="article" />
            <meta name="og:title" content={title} />
            <meta name="og:description" content={excerpt} />
            <meta name="twitter:title" content={title} />
            <meta name="twitter:description" content={excerpt} />
        </Head>
    );
}

function PostLayoutWrapper({
    children,
    meta,
    ...rest
}: {
    children: React.Node,
    meta?: PostMeta
}) {
    return (
        <Layout>
            {meta && <PostHead {...meta} />}
            <Post {...rest}>
                {children}
                <StyledFooter>
                    <Link href="/" passHref>
                        <Button as="a">Back to all posts</Button>
                    </Link>
                </StyledFooter>
            </Post>
        </Layout>
    );
}
