// @flow

import App, {Container} from 'next/app';
import Head from 'next/head';
import Router from 'next/router';
import store, {root} from '../store';
import {hostname} from '../config';
import {downcase} from '../utilities';
import {withMDX} from '../components/application';
import GlobalCSS from '../components/global-css';
import {page} from '../config/styles';

export default class Application extends App {
    static async getInitialProps({
        Component,
        ctx
    }: {
        /* This 'any' should be NextComponentType,
         * but import {NextComponentType} from 'next' crashes compilation
         * with "Module not found: Can't resolve 'aws-sdk'" in:
         * ./node_modules/fsevents/node_modules/node-pre-gyp/lib
         */
        Component: any,
        ctx: Object
    }) {
        const pageProps = Component.getInitialProps
            ? await Component.getInitialProps(ctx)
            : {};

        return {pageProps};
    }

    updateRoute = (route: string): void => store.set('location', route);

    constructor(props: {router: {route: string}}) {
        super(props);
        this.updateRoute(props.router.route);
        Router.events.on('routeChangeComplete', this.updateRoute);
    }

    render() {
        const {
            Component,
            pageProps,
            router: {route}
        } = this.props;
        const RootedContainer = root(Container);
        const ComponentWithMDX = withMDX(Component);

        return (
            <RootedContainer>
                <Head>
                    <link
                        rel="canonical"
                        href={`https://${hostname}${route.slice(1) ? route : ''}`}
                    />
                    <link
                        rel="apple-touch-icon"
                        sizes="180x180"
                        href="/static/apple-touch-icon.png"
                    />
                    <link
                        rel="icon"
                        type="image/png"
                        sizes="32x32"
                        href="/static/favicon-32x32.png"
                    />
                    <link
                        rel="icon"
                        type="image/png"
                        sizes="16x16"
                        href="/static/favicon-16x16.png"
                    />
                    <link
                        rel="icon"
                        sizes="48x48"
                        type="image/icon"
                        href="/static/favicon.ico"
                    />
                    <link
                        rel="preload"
                        as="font"
                        href={`/static/fonts/${downcase(page.fontFamily)}-300.woff2`}
                        crossOrigin="anonymous"
                    />
                    <link rel="manifest" href="/static/site.webmanifest" />
                    <meta content="text/html; charset=UTF-8" httpEquiv="content-type" />
                    <meta content="en" httpEquiv="content-language" />
                    <meta name="robots" content="index, follow" />
                    <meta
                        name="viewport"
                        content="width=device-width, initial-scale=1.0"
                    />
                </Head>
                <GlobalCSS />
                <ComponentWithMDX {...pageProps} />
            </RootedContainer>
        );
    }
}
